import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { FuncionModel } from '../models/gestion-academica/funcion.model';
import { TrayectoriaModel } from '../models/gestion-academica/trayectoria.model';
@Injectable({
    providedIn: 'root'
})
export class TrayectoriaService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllTrayectorias(): Observable<TrayectoriaModel[]> {
        return this.http.get<TrayectoriaModel[]>(`${environment.backend.gestion_academica}api/trayectoria/v1/get-all`);
    }

}