import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { AdjudicacionDocenteModel } from 'src/app/commons/models/gestion-academica/adjudicacion-docente.model';
import { CompromisoModel } from 'src/app/commons/models/gestion-academica/compromiso.model';
import { DatosModulosReducidoModel } from 'src/app/commons/models/gestion-academica/datos-modulos-reducido.model';
import { FacultadModel } from 'src/app/commons/models/gestion-academica/facultad.model';
import { FuncionModel } from 'src/app/commons/models/gestion-academica/funcion.model';
import { JerarquiaActualModel } from 'src/app/commons/models/gestion-academica/jerarquia-actual.model';
import { PeriodoModel } from 'src/app/commons/models/gestion-academica/periodo.model';
import { PublicacionDocenteModel } from 'src/app/commons/models/gestion-academica/publicacion-docente.model';
import { TrayectoriaModel } from 'src/app/commons/models/gestion-academica/trayectoria.model';
import { DocenteService } from 'src/app/commons/services/docente.service';
import { CompromisoService } from 'src/app/commons/services/compromiso.service';
import { FacultadService } from 'src/app/commons/services/facultad.service';
import { FuncionService } from 'src/app/commons/services/funcion.service';
import { JerarquiaService } from 'src/app/commons/services/jerarquia.service';
import { TrayectoriaService } from 'src/app/commons/services/trayectoria.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { UtilValidation } from 'src/app/commons/util/util.validation';
import { getPeriodos, guardarPeriodo, paginationPeriodo, resetPeriodo } from 'src/app/store/actions/periodo.actions';
import { AppState } from 'src/app/store/app.reducer';
import { DatosModuloService } from 'src/app/commons/services/datos-modulos.service';
import { Chart } from 'chart.js';
import { ModalCantidadFuncionariosComponent } from 'src/app/commons/components/modal-cantidad-funcionarios/modal-cantidad-funcionarios.component';


interface Contador {
  nombre: string;
  cantidad: number;
}
@Component({
  selector: 'app-reporte-compromisos',
  templateUrl: './reporte-compromisos.component.html',
  styles: [
  ]
})




export class ReporteCompromisosComponent implements OnInit, OnDestroy {


  @ViewChild(ModalCantidadFuncionariosComponent, { static: false })
  public modalCantidadDocentes: ModalCantidadFuncionariosComponent;

  formReporte: FormGroup;
  fecha: Date = new Date();
  anio = this.fecha.getFullYear();
  listaFacultades: FacultadModel[] = [];
  listaFunciones: FuncionModel[] = [];
  listaCompromisos: CompromisoModel[] = [];
  listaCompromisosFiltrada: CompromisoModel[] = [];
  listaJerarquias: String[] = [];
  listaRutParaJerarquia: string[] = [];
  listaJerarquiasActuales: JerarquiaActualModel[] = [];
  listaJornadas: String[] = ['Menos de 1/2 Jornada', '1/2 Jornada', '3/4 Jornada', 'Completa'];
  listaTrayectorias: TrayectoriaModel[] = [];
  funcionesFiltradas: FuncionModel[] = [];
  totalTrayectorias: number = 1;
  totalJornadas: number = 1;
  totalJerarquias: number = 1;
  totalPublicacionesSolicitadas: number = 0;
  totalAdjudicacionesSoliciatas: number = 0;
  valorBoton: String = "Mostrar Inexistentes";
  verNoAplica: Boolean = false;

  graficoJornadas: Chart;
  graficoTrayectorias: Chart;

  modulosEscuelas: DatosModulosReducidoModel[] = [];


  docentesPublicaciones: PublicacionDocenteModel[] = [];
  docentesAdjudicaciones: AdjudicacionDocenteModel[] = [];

  //FUNCIONES
  decano: number = 0;
  directorEscuela: number = 0;
  directorCarrera: number = 0;
  directorPostgrado: number = 0;
  secretarioAcademico: number = 0;
  coordinadorCentroPractica: number = 0;
  directorInvestigacion: number = 0;
  coordinadorLinea: number = 0;
  directorVcm: number = 0;
  coordinadorVcm: number = 0;
  encargadoVcm: number = 0;
  directorCentros: number = 0;
  jefeGruposTrabajo: number = 0;
  noAplica: number = 0;
  funcionFiltrada = {};
  trayectoriaFiltrada = {};
  jornadaFiltrada = {};
  jerarquiaFiltrada = {};
  modulosDocenciaFiltrada = {};
  docentesFiltrados = {}

  contadorFunciones = 0;


  listadoFechas: number[] = [this.anio - 1, this.anio];

  constructor(
    private appService: AppService,
    private facultadService: FacultadService,
    private funcionService: FuncionService,
    private compromisoService: CompromisoService,
    private trayectoriaService: TrayectoriaService,
    private jerarquiaService: JerarquiaService,
    private docenteService: DocenteService,
    private datosModulosService: DatosModuloService,
    private toastr: ToastrService,
  ) {
    this.appService.pageTitle = 'Reporte de Compromisos';

  }

  ngOnInit(): void {
    this.getFacultades();
    this.iniciarFormulario();
    this.obtenerCompromisoByAnio();
    this.obtenerFunciones();
    this.getTrayectorias();
  }
  ngOnDestroy(): void {

  }

  getModulosEscuelasTotales(anio: number): void {
    this.datosModulosService.getDatosModuloResumido(anio).subscribe(value => {
      this.modulosEscuelas = value;
      this.filtrarModulosDocentes()
    }, error => {
      this.toastr.error("Ha ocurrido un error al intentar obtener los modulos de escuela (resumido)", "Error");
    })



  }


  getPublicacionesDocentes() {
    let listaRutPublicaciones: String[] = [];
    for (let index = 0; index < this.listaCompromisosFiltrada.length; index++) {
      const element = this.listaCompromisosFiltrada[index];
      listaRutPublicaciones.push(element.rutDocente.rut);
    }
    this.docenteService.getPublicacionesByDocentes(listaRutPublicaciones, this.formReporte.get("anio").value).subscribe(value => {
      this.docentesPublicaciones = value;
    })
  }

  getAdjudicacionesDocentes() {
    let listaRutAdjudicaciones: String[] = [];
    for (let index = 0; index < this.listaCompromisosFiltrada.length; index++) {
      const element = this.listaCompromisosFiltrada[index];
      listaRutAdjudicaciones.push(element.rutDocente.rut);

    }
    this.docenteService.getAdjudicacionesByDocentes(listaRutAdjudicaciones, this.formReporte.get("anio").value).subscribe(value => {
      this.docentesAdjudicaciones = value;
    })
  }

  getTrayectorias() {
    this.trayectoriaService.getAllTrayectorias().subscribe(value => {
      this.listaTrayectorias = value;
    })
  }

  getFacultades() {
    this.facultadService.getAllFacultades().subscribe(value => {
      this.listaFacultades = value;
    })
  }

  obtenerFunciones() {
    this.funcionService.getAllFuncion().subscribe(value => {
      this.listaFunciones = value;
    })
  }

  obtenerCompromisoByAnio() {
    let anio = this.formReporte.get("anio").value;
    this.formReporte.get("facultad").setValue("SF");
    this.compromisoService.getCompromisoByAnio(anio).subscribe(value => {
      this.listaCompromisos = value;
      this.listaCompromisosFiltrada = value;
      this.filtrarFunciones();
      this.filtrarTrayectorias();
      this.filtrarJornadas();
      this.filtrarJerarquias()
      this.filtrarCantidadPublicacionesAdjudicacioens();
      this.getAdjudicacionesDocentes();
      this.getPublicacionesDocentes();
      this.getModulosEscuelasTotales(anio);

    })
  }

  iniciarFormulario() {
    this.formReporte = new FormGroup({
      anio: new FormControl(0),
      facultad: new FormControl(""),
    })
    this.formReporte.get("anio").setValue(this.listadoFechas[0].toString());
    this.formReporte.get("facultad").setValue("SF");
  }

  reiniciarFunciones() {
    this.decano = 0;
    this.directorEscuela = 0;
    this.directorCarrera = 0;
    this.directorPostgrado = 0;
    this.secretarioAcademico = 0;
    this.coordinadorCentroPractica = 0;
    this.directorInvestigacion = 0;
    this.coordinadorLinea = 0;
    this.directorVcm = 0;
    this.coordinadorVcm = 0;
    this.encargadoVcm = 0;
    this.directorCentros = 0;
    this.jefeGruposTrabajo = 0;
    this.noAplica = 0;
  }

  filtrarFunciones() {
    let contador = {};

    this.contadorFunciones = 0;

    this.listaCompromisosFiltrada.forEach(value => {
      // Definimos el objeto contador
      let elemento = value.funcion.id;
      // Si el elemento no existe en el contador, lo agregamos con valor 1
      if (!contador[elemento]) {
        contador[elemento] = 1;
      } else {
        // Si el elemento ya existe en el contador, aumentamos su valor en 1
        contador[elemento]++;
      }

      if (elemento != 14) {
        this.contadorFunciones++;
      }

    })
    this.funcionFiltrada = contador;
  }

  filtrarTrayectorias() {
    let contador = {};
    let trayectoriaFiltradaLabel: String[] = [];
    let trayectoriaidLabel: number[] = [];
    let trayectoriaFiltadaValores: number[] = [];
    let seAgrego = false;
    this.listaCompromisosFiltrada.forEach(value => {
      // Definimos el objeto contador
      let elemento;

      if (value.trayectoriaId == null) {
        elemento = 1;
      } else {
        elemento = value.trayectoriaId.id;
      }
      // Si el elemento no existe en el contador, lo agregamos con valor 1
      if (!contador[elemento]) {
        contador[elemento] = 1;
        if (value.trayectoriaId != null) {
          trayectoriaFiltradaLabel.push(value.trayectoriaId.descripcion);
          trayectoriaidLabel.push(elemento);
        } else {
          if (!seAgrego) {
            trayectoriaFiltradaLabel.push("Revisar Distribución de Jornada");

            trayectoriaidLabel.push(1);
            seAgrego = true;
          }
        }
      } else {
        // Si el elemento ya existe en el contador, aumentamos su valor en 1
        contador[elemento]++;
      }
    })
    console.log(trayectoriaFiltadaValores)
    this.trayectoriaFiltrada = contador;
    console.log(this.trayectoriaFiltrada)
    trayectoriaidLabel.forEach(element => trayectoriaFiltadaValores.push(this.trayectoriaFiltrada[`${element}`]));
    this.totalTrayectorias = this.listaCompromisosFiltrada.length;
    const graficoTrayectoriaDiv = document.getElementById('graficoTrayectoriaDiv') as HTMLCanvasElement;

    let listaBackgroundColor = []
    let listaBorderColor = []

    trayectoriaFiltadaValores.forEach(element => {
      let r = Math.floor(Math.random() * (255 - 0 + 1) + 0)
      let g = Math.floor(Math.random() * (255 - 0 + 1) + 0)
      let b = Math.floor(Math.random() * (255 - 0 + 1) + 0)

      listaBackgroundColor.push(`rgba(${r},${g},${b},0.2)`)
      listaBorderColor.push(`rgba(${r},${g},${b},1)`)
    });


    this.graficoTrayectorias = new Chart(graficoTrayectoriaDiv, {
      type: 'pie',
      data: {
        labels: trayectoriaFiltradaLabel,
        datasets: [
          {
            label: 'Total de Trayectorias',
            data: trayectoriaFiltadaValores,
            backgroundColor: listaBackgroundColor,
            borderColor: listaBorderColor,
            borderWidth: 1,
          },
        ],
      },
      options: {
        scales: {
          y: {
            beginAtZero: true,
          },
        },
      },
    });
  }

  filtrarModulosDocentes() {
    let contador = {};
    this.listaCompromisosFiltrada.forEach(value => {
      // Definimos el objeto contador
      let elemento;
      elemento = value.rutDocente.escuela.codigo;
      // Si el elemento no existe en el contador, lo agregamos con valor 1
      if (!contador[elemento]) {
        contador[elemento] = value.modulosDocencia;
      } else {
        // Si el elemento ya existe en el contador, aumentamos su valor en 1
        contador[elemento] += value.modulosDocencia;
      }
    })
    this.modulosDocenciaFiltrada = contador;
  }

  filtrarJerarquias() {
    let contador = {};
    this.listaRutParaJerarquia = [];
    this.listaCompromisosFiltrada.forEach(value => {
      this.listaRutParaJerarquia.push(value.rutDocente.rut);
    })

    let rutPrueba: string[] = ['9381452-5', '9157441-1', '9980030-5', '19953623-0', '9614458-K'];

    //TODO: CAMBIAR RUTPUEBA POR LISTARUTPARAJERARQUIA
    this.jerarquiaService.getJerarquiaActualMasivo(this.listaRutParaJerarquia).subscribe(value => {
      this.listaJerarquiasActuales = value;
      this.filtrarJerarquiasActuales();
    })


  }

  filtrarJerarquiasActuales() {
    let contador = {}
    this.listaJerarquiasActuales.forEach(value => {
      let elemento;

      elemento = value.jerarquia

      if (!contador[elemento]) {
        contador[elemento] = 1;
        this.listaJerarquias.push(elemento)
      } else {
        contador[elemento]++;
      }
    })
    console.log(contador)
    this.jerarquiaFiltrada = contador;
    this.totalJerarquias = this.listaJerarquiasActuales.length;
  }

  filtrarJornadas() {
    let jornadaFiltradaLabel: String[] = [];
    let jornadaFiltadaValores: number[] = [];
    let contador = {};
    this.listaCompromisosFiltrada.forEach(value => {
      // Definimos el objeto contador
      let elemento;

      if (value.rutDocente.jornada == null) {
        elemento = 1;
      } else {
        elemento = value.rutDocente.jornada;
      }
      // Si el elemento no existe en el contador, lo agregamos con valor 1
      if (!contador[elemento]) {
        contador[elemento] = 1;
        jornadaFiltradaLabel.push(elemento);
      } else {
        // Si el elemento ya existe en el contador, aumentamos su valor en 1
        contador[elemento]++;
      }
    })
    this.jornadaFiltrada = contador;
    jornadaFiltradaLabel.forEach(element => jornadaFiltadaValores.push(this.jornadaFiltrada[`${element}`]));
    console.log(jornadaFiltadaValores)
    this.totalJornadas = this.listaCompromisosFiltrada.length;
    const graficoJornadaDiv = document.getElementById('graficoJornadaDiv') as HTMLCanvasElement;

    let listaBackgroundColor = []
    let listaBorderColor = []

    jornadaFiltadaValores.forEach(element => {
      let r = Math.floor(Math.random() * (255 - 0 + 1) + 0)
      let g = Math.floor(Math.random() * (255 - 0 + 1) + 0)
      let b = Math.floor(Math.random() * (255 - 0 + 1) + 0)

      listaBackgroundColor.push(`rgba(${r},${g},${b},0.2)`)
      listaBorderColor.push(`rgba(${r},${g},${b},1)`)
    });


    this.graficoJornadas = new Chart(graficoJornadaDiv, {
      type: 'pie',
      data: {
        labels: jornadaFiltradaLabel,
        datasets: [
          {
            label: 'Total de Jornadas',
            data: jornadaFiltadaValores,
            backgroundColor: listaBackgroundColor,
            borderColor: listaBorderColor,
            borderWidth: 1,
          },
        ],
      },
      options: {
        scales: {
          y: {
            beginAtZero: true,
          },
        },
      },
    });
  }

  filtrarCantidadPublicacionesAdjudicacioens() {
    this.totalPublicacionesSolicitadas = 0;
    this.totalAdjudicacionesSoliciatas = 0;
    this.listaCompromisosFiltrada.forEach(value => {
      this.totalPublicacionesSolicitadas += value.publicacionesRequerimiento;
      this.totalAdjudicacionesSoliciatas += value.adjudicacionesRequerimiento;
    })
    console.log(this.totalPublicacionesSolicitadas);
    console.log(this.totalAdjudicacionesSoliciatas);
  }

  volverAFiltrar() {
    this.filtrarFunciones();
    this.filtrarTrayectorias();
    this.filtrarJornadas();
    this.filtrarCantidadPublicacionesAdjudicacioens();
    this.filtrarModulosDocentes();
  }

  filtrarPorFacultad() {
    this.listaCompromisosFiltrada = []
    if (this.formReporte.get("facultad").value == 'SF') {
      this.listaCompromisosFiltrada = this.listaCompromisos;
    } else {
      this.listaCompromisosFiltrada = this.listaCompromisos.filter(value => value.rutDocente.facultad.codigo == this.formReporte.get("facultad").value.toString())
      this.filtrarDocentes();
    }
    this.volverAFiltrar();

  }


  filtrarDocentes() {
    let contador = {};
    this.listaCompromisosFiltrada.forEach(value => {
      // Definimos el objeto contador
      let elemento;
      elemento = value.funcion.id;
      // Si el elemento no existe en el contador, lo agregamos con valor 1
      if (!contador[elemento]) {
        contador[elemento] = [];
        contador[elemento].push(value.rutDocente.nombre + " " + value.rutDocente.apellido)
      } else {
        // Si el elemento ya existe en el contador, aumentamos su valor en 1
        contador[elemento].push(value.rutDocente.nombre + " " + value.rutDocente.apellido);
      }
    })
    this.docentesFiltrados = contador;
    console.log(this.docentesFiltrados);
  }

  botonNoAplica() {
    if (this.verNoAplica) {
      this.verNoAplica = false;
      this.valorBoton = "Mostrar Inexistentes";
    } else {
      this.verNoAplica = true;
      this.valorBoton = "Quitar Inexistentes";
    }
  }

  modalVer(academicos) {
    console.log(academicos)
    this.modalCantidadDocentes.openModal(academicos)
  }
}