import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from '../../../environments/environment';
import { DecanoModel } from '../models/gestion-academica/decano.model';
import { DatosModulosReducidoModel } from '../models/gestion-academica/datos-modulos-reducido.model';

@Injectable({
    providedIn: 'root'
})

export class DatosModuloService {

    constructor(
        private http: HttpClient,
    ) { }

    public getDatosModuloResumido(anio: number): Observable<DatosModulosReducidoModel[]> {
        return this.http.get<DatosModulosReducidoModel[]>(`${environment.backend.gestion_academica}api/banner/v1/get-datos-modulos-reducido/${anio}`);
    }


}